/* Plumber for baseline grid */
/* Paged and print specific styles are collected in paged.css */
/* Pagedjs/paged media specific styles */
@page {
  size: 110mm 200mm;
  margin-top: 15mm;
  margin-right: 10mm;
  margin-bottom: 25mm;
  margin-left: 15mm;
  /* bleed: 6mm;
   marks: crop cross; */
  background-color: white;
  @footnote {
    float: bottom;
  }
  @bottom-center {
    content: counter(page);
  }
}
@media print {
  @page {
    bleed: 4mm;
    marks: crop cross;
  }
}
@page :left {
  margin-right: 15mm;
  margin-left: 10mm;
  @top-center {
    content: string(chapterTitle);
  }
}
@page :right {
  @top-left {
    content: string(articleTitle);
  }
  @top-right {
    content: string(author);
  }
}
@page :first {
  @top-left {
    content: "";
  }
  @bottom-right-corner {
    content: "";
  }
}
.pagedjs_page {
  background-color: transparent;
}

.pagedjs_sheet {
  z-index: 1;
}

.pagedjs_margin-bottom-left-corner,
.pagedjs_margin-bottom-right-corner {
  font-size: 1.5rem;
  font-family: Ortica, serif;
}

.pagedjs_footnote_area {
  margin-top: 1rem;
}

/* Setting variables */
h1 {
  string-set: chapterTitle content(text);
}

h2 {
  counter-increment: countChapter;
  string-set: articleTitle content(text);
  counter-set: footnote-marker 0 footnote 0;
  string-set: author "";
}

span.author {
  string-set: author content(text);
}

/* Move the footnotes to the footer */
span.footnote {
  float: footnote;
}

